import { HeadingPlugin } from '@ali/4ever-bamboo';
import { BlockJSON } from '@ali/4ever-cangjie';

export default function getHeadingModels() {
  const { HeadingTypes } = HeadingPlugin;
  return Object.keys(HeadingTypes).map(key => ({
    klass: 'block',
    type: HeadingTypes[key],
  })) as BlockJSON[];
}